import React from 'react';
import YouTube from 'react-youtube';

class Appp extends React.Component {
  _onReady(event) {
    //      
    event.target.playVideo();
  }

  render() {
    const opts = {
      height: '390',
      width: '640',
      playerVars: {
        //       
        autoplay: 1,
      },
    };

    return (
      <YouTube
        videoId="https://www.youtube.com/watch?v=uWSaJJ9UEm8" //  YouTube  ID 
        opts={opts}
        onReady={this._onReady}
      />
    );
  }
}

export default Appp;
